import React, {Component } from 'react';
import { connect } from 'react-redux';
import { actionCreator } from './store';

import { Redirect } from 'react-router-dom';
import { Form, Icon, Input, Button, Radio, Breadcrumb, message } from 'antd';
import axios from 'axios';

class NormalLoginForm extends Component {
    /**
     * 
     */
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if(!err) {
                console.log('Received values of form: ', values);
                /**
                    |			|  |     |
                    |name        |Y        |string  |
                    |identifier  |Y	       |string  |
                    |type        |Y		   |string  |
                    |depId       |Y        |int     |
                */
                const formData = {      //
                    name: values.name,
                    identifier: values.identifier,
                    type: values.type,
                    depId: values.depId
                }
                axios.post('/api/checkTable/insert',formData).then(res=> {
                    const data = res.data;
                    if(data.status === 1) {
                        message.success('');
                        this.props.changeSubmitSuccess(true);       //submitSuccess true
                    }else {
                        message.error('');
                    }    
                }).catch(error=>{
                    message.error(error.message);
                });
            }else {
                message.info('!')
            }
        });
    };
    

    componentWillUnmount() {
        this.props.changeSubmitSuccess(false);
    }


    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 6 },
            wrapperCol: { span: 14 },
        };


        // 
        const formDisabled = false;
        // 
        const { submitSuccess } = this.props;
        if(submitSuccess) {
            return <Redirect to="/main/checktable/manage" />
        }


        return (
            <div className="page">
                {/*  */}
                <Breadcrumb className="path">
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
                {/*  */}
                <div className="contentWrap" style={{paddingTop: "50px"}}>
                    <Form {...formItemLayout} onSubmit={this.handleSubmit}>
                        <Form.Item label="">
                        {getFieldDecorator('name', {
                            rules: [{ required: true, message: '!' }],
                        })(
                            <Input placeholder="" disabled={formDisabled}/>,
                        )}
                        </Form.Item>
                        <Form.Item label="">
                        {getFieldDecorator('identifier', {
                            rules: [{ required: true, message: '!' }],
                        })(
                            <Input placeholder=""  disabled={formDisabled} />,
                        )}
                        </Form.Item>

                        <Form.Item label="">
                        {getFieldDecorator('type', {
                            rules: [{ required: true,  message: '!' }]
                        })(
                            <Radio.Group>
                                <Radio value=""></Radio>
                                <Radio value=""></Radio>
                                <Radio value=""></Radio>
                                <Radio value=""></Radio>
                                <Radio value=""></Radio>
                            </Radio.Group>,
                        )}
                        </Form.Item>

                        <Form.Item label="">
                        {getFieldDecorator('depId', {
                            rules: [{ required: true,  message: '!' }],
                        })(
                            <Input placeholder=""  disabled={formDisabled} />,
                        )}
                        </Form.Item>

                        <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
                            <Button type="primary" htmlType="submit" disabled={formDisabled}>
                            
                            </Button>
                        </Form.Item>
                    </Form>
                </div>
            </div> 
        );
    }
}


const mapState = (state)=> {
    return {
      submitSuccess: state.getIn(['checkTable','submitSuccess'])
    }
  };
  
  /**
   *   state props 
   */
  const mapDispatch = (dispatch)=> {
    return {
      changeSubmitSuccess(value) {  //
        dispatch(actionCreator.changeSubmitSuccess(value));
      }
    }
  };

const WrappedNormalLoginForm = Form.create({ name: 'organization_add' })(NormalLoginForm);

export default connect(mapState,mapDispatch)(WrappedNormalLoginForm);