import React, { Component } from 'react';
import { connect } from 'react-redux';
import {  actionCreator } from './store';
import { Breadcrumb, Table, Tag,  Modal, message } from 'antd';

import axios from 'axios';
import './style.css';

  
class RoleManage extends Component { 
    constructor(props) {
      super(props);
      this.deleteItem = this.deleteItem.bind(this);
    }

    componentDidMount() {
      // 
      this.props.changeRoleList();
        
    }
    /**
     * 
     * @param {} text 
     */
    deleteItem(text) {
      Modal.confirm({
          title: '?',
          okText: 'Yes',
          okType: 'danger',
          okButtonProps: {
            disabled: false,
          },
          cancelText: 'No',
          onOk() {
            axios.post('/api/role/delete',{
              id: text.id
            }).then(res=>{
              console.log(res);
              message.success('',2);
            }).catch(error=> {
              message.error(error.message);
            });
            console.log('OK,');
             //item
          }
        });
    }


    render() {
        //
        const columns = [
          {
            title: 'ID',
            dataIndex: 'id',
            key: 'id',
          },
          {
            title: '',
            dataIndex: 'name',
            key: 'name',
          },
          {
            title: '',
            key: 'action',
            render:()=>{
              return (
                <span>
                  <Tag color="blue" onClick={()=> {console.log('xxx')}}></Tag>
                  <Tag color="magenta" onClick={(text)=>{this.deleteItem(text)}}></Tag>
                </span>
              )
            }
          }
        ];
      
      
        //
        const {roleList, pagenationProps } = this.props; 
        const data = roleList.toJS();                 // immutable js
        const JSpagenationProps = pagenationProps.toJS();   //immutable  js

        return (
            <div className="page">
                {/*  */}
                <Breadcrumb className="path">
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
                {/*  */}
                <div className="contentWrap">
                    <Table bordered pagenation={JSpagenationProps} columns={columns} dataSource={data} />
                </div>
            </div>
        )
    }
}


// store props
const mapState = (state)=> {
  return {
    roleList: state.getIn(['role','roleList']),
    pagenationProps: state.getIn(['role','pagenationProps'])
  }
};

const mapDispatch = (dispatch)=> {
  return {
    changeRoleList() {
      dispatch(actionCreator.getRoleList());
    }
  }
};

export default connect(mapState,mapDispatch)(RoleManage);