import React, { Component } from 'react';
import { connect } from 'react-redux';
import { actionCreator } from './store';
import { Breadcrumb, Table, Tag, Divider } from 'antd';
import './style.css';


const dataSource = [
    {
        key: '1',
        userName: '',
        roleName: '',
        deptId: 'id'
    },
    {
      key: '2',
      userName: '',
      roleName: '',
      deptId: 'id'
  },
];

const columns = [
  {
    title: 'ID',
    dataIndex: 'key',
    key: 'id'
  },
  {
    title: '',
    dataIndex: 'userName',
    key: 'userName',
  },
  {
    title: '',
    dataIndex: 'roleName',
    key: 'roleName',
    render: (text,item)=> {
      return (
        <span>
          <span>{text}</span>
          <Divider type="vertical" />
          {
            text === ''?  <Tag color="green" onClick={(item)=>{console.log('')}}></Tag> 
                    :  <Tag onClick={(item)=>{console.log('')}}></Tag>
          }
        </span>
      )
    }
  },
  {
    title: '',
    dataIndex: 'deptId',
    key: 'deptId',
  },

  {
    titel:'',
    key: 'action',
    render: () => {
      return (
        <span>
          <Tag color="blue" onClick={console.log('updata user')}></Tag>
          <Tag color="magenta" onClick={(text)=>{console.log('delete user ')}}></Tag>
        </span>
      )
    }
  }
];


class UserManage extends Component {
    componentDidMount() {
        //
        this.props.getUserList();

    }
    render() {
        return (
            <div className="page">
                {/*  */}
                <Breadcrumb className="path">
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
                {/*  */}
                <div className="contentWrap">
                    <Table bordered  dataSource={dataSource} columns={columns} loading={false}/>
                </div>
            </div>
        )
    }
}



const mapState = (state)=> {
  return {

  }
};

const mapDispatch = (dispatch)=> {
  return {
    getUserList() {
      dispatch(actionCreator.getUserList());
    }
  }
}


export default connect(mapState,mapDispatch)(UserManage);