import React, { Component } from 'react';
import { Breadcrumb } from 'antd';
import { Link } from 'react-router-dom';

import './style.css';

//table
import { Table,Divider,Button,Icon } from 'antd';

//
import { Input } from 'antd';
const { Search } = Input;

//tabel
const columns = [
  {
    title: 'ID',
    dataIndex: 'age',
    key: 'age',
  },
  {
    title: 'ID',
    dataIndex: 'age',
    key: 'age',
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },  
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: '',
    key: 'action',
    render: (text, record) => (
      <span>
        <a></a>
        <Divider type="vertical" />
        <a></a>
      </span>
    ),
  },
];

//table
const data = [
  {
    key: '1',
    name: 'John Brown',
    age: 32,
    address: 'New York No. 1 Lake Park',
    tags: ['nice', 'developer'],
  },
  {
    key: '2',
    name: 'Jim Green',
    age: 42,
    address: 'London No. 1 Lake Park',
    tags: ['loser'],
  },
  {
    key: '3',
    name: 'Joe Black',
    age: 32,
    address: 'Sidney No. 1 Lake Park',
    tags: ['cool', 'teacher'],
  },
];

class WoundeManage extends Component {
    render() {
        return (
            <div className="page">
                {/*  */}
                <Breadcrumb className="path">
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
                {/*  */}
                <div className='contentWrap'>
                    <div style={{width:'90%',margin:'20px auto',height:'80px'}}>
                    <Search
                    className="searchClass"
                    placeholder="input search text"
                    enterButton="Search"
                    size="large"
                    onSearch={value => console.log(value)}
                    />
                    <Link to="/main/accident/wounde/add">
                      <Button style={{float:'right',width:'79px',height:'40px',margin:'20px 0px'}}>
                      <Icon type="plus" /></Button>
                    </Link>
                    </div>
                    <Table
                    className="tableClass"
                    bordered="true"
                    columns={columns} dataSource={data} />
                </div>
            </div>
        )
    }
}

export default WoundeManage;