import { fromJS } from 'immutable';
import * as actionTypes from './actionTypes';

const defaultState = fromJS({
    firstIndicatorList:[],  //
    pagenationProps: {
        pageSize: 5,  //
        current: 1,  //
        total: 1,    //
        showSizeChanger: true,  //pageSize
        showQuickJumper: true, //
    },

    //idname select
    selectList: [],

    //id
    checkTableId: undefined,
    //
    modal_visible: false,   //modal
    modal_project: ''       //project 
});

const reducer = (state=defaultState, action) => {
    switch(action.type) {
        case actionTypes.CHANGE_MODAL_VISIBLE:
            return state.set('modal_visible',action.modal_visible); 
        case actionTypes.CHANGE_SELECT_LIST:
            return state.set('selectList',fromJS(action.list)); //id selectlist
        default:
            return state;
    }
}
export default reducer;