import React, { Component } from 'react';
import { Link } from 'react-router-dom';
//store
import { connect } from 'react-redux';
import { actionCreator as firstIndicatorActions } from '../firstIndicatorManage/store';   //checktableselect
import { actionCreator } from './store';

import { Table, Button, Icon, Tag, Divider,  Breadcrumb, Select } from 'antd';

class SecondIndicatorManage extends Component {
    

    render() {
        console.log(this.props,'second indicator')
        //table
        const dataSource = [
            { id: 1, content: ',', addDate: '2012-5-23' },
            { id: 2, content: '', addDate: '2012-5-23' }
        ];

        //table 
        const ccc = [
            { title: 'ID', dataIndex: 'id', key: 'id' },
            { title: '', dataIndex: 'content', key: 'project' },
            { title: '', dataIndex: 'addDate', key: 'addDate' },
            {
                title: '',
                key: 'action',
                render: ()=>(<span>
                    <Link to="/main/secondIndicator/manage/detail">
                        <Tag color="blue"></Tag>
                    </Link>
                    <Divider type="vertical"/>
                    <Tag color="red"></Tag>
                </span>)
            }
        ];




        return (
            <div className="page">
                {/*  */}
                <Breadcrumb className="path">
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
                {/*  */}
                <div className="contentWrap">
                    <div style={{width:'90%',margin:'20px auto',height:'80px'}}>
                        <Select
                            size="large"
                            style={{ width: 200, display:"inline-block",margin: "20px 0" }}
                            placeholder="Select a checktable"
                            onChange={(e,v)=>{console.log(e,v,'selet change ')}}
                        >
                            <Select.Option value="1">1</Select.Option>
                            <Select.Option value="2">2</Select.Option>
                            <Select.Option value="3">3</Select.Option>
                        </Select>   
                        <Select
                            size="large"
                            style={{ width: 200, display:"inline-block",margin: "20px 0" }}
                            placeholder="Select a checktable"
                            onChange={(e,v)=>{console.log(e,v,'selet change ')}}
                        >
                            {/* <Select.Option value="item1">item1</Select.Option>
                            <Select.Option value="item2">item2</Select.Option>
                            <Select.Option value="item3">item3</Select.Option> */}
                        </Select>   
                        {/* <Search
                            className="searchClass"
                            placeholder="input search text"
                            enterButton="Search"
                            size="large"
                            onSearch={value => console.log(value)}
                        /> */}
                        <Link>
                        <Button style={{float:'right',width:'79px',height:'40px',margin:'20px 0px'}}><Icon type="plus" /></Button>
                        </Link>
                    </div>
                    <Table
                        className="tableClass"
                        bordered="true"
                        columns={ccc} dataSource={dataSource} />
                </div>
            </div>
        )
    }
}


// store props
const mapState = (state)=> {
    return {
        secondIndicatorList: state.getIn(['secondIndicator','secondIndicatorList']),    //
        pagenationProps: state.getIn(['secondIndicator','pagenationProps']),        //

        SelectCheckTableList: state.getIn(['firstIndicator','selectList']),     //idname list
        SelectFirstIndicatorList: state.getIn(['secondIndicator','SelectFirstIndicatorList']),  //idnamelist
    }
  };

//storeprops
const mapDispatch = (dispatch)=> {
    return {
        getSelectCheckTableList() { //id name 
            dispatch(firstIndicatorActions.getSelectList());
        },
        getSelectFirstIndicatorList() { //
            dispatch(actionCreator.getSelectFirstIndicatorList());
        }
    }
};
export default connect(mapState, mapDispatch)(SecondIndicatorManage);