import * as actionTypes from './actionTypes';
import axios from 'axios';



/**
 * checkTable
 */
export const getCheckTableList = () => {
    return (dispatch) => {
        axios.get('api/checkTable/getList').then(res=>{
            console.log(res)
            const data = res.data;
            if(data.status === 1) {  //
                dispatch(
                    changeCheckTalbeList(
                        data.data.list,
                        data.data.pageSize,
                        data.data.pageNum,
                        data.data.total
                    )
                )
            }else {

            }
        }).catch(error=>{
            console.log(error)
        })
    }
}


/**
 * 
 * @param {}} submitSuccess 
 */
export const changeSubmitSuccess = (submitSuccess)=> ({
    type: actionTypes.CHANGE_SUBMIT_SUCCESS,
    submitSuccess
});

/**
 * 
 * @param {table} list 
 * @param {} pageSize 
 * @param {} pageNum 
 * @param {} total 
 */
const changeCheckTalbeList = (list, pageSize, pageNum, total)=> ({
    type: actionTypes.CHANGE_CHECKTABLE_LIST,
    list,
    pageSize,
    pageNum,
    total
});

