import React, { Component } from 'react';
import {  Menu,  Icon } from 'antd';
import {Link} from 'react-router-dom';

import './style.css';

const { SubMenu } = Menu;
class LeftNav extends Component {
    render() {
        return (
           <div>
          {/* logo  */}
          <div className="logo"><span>&nbsp;DPM</span></div>

          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
            <Menu.Item key="1"><Link to="/main"><span><Icon type="home"/><span></span></span></Link></Menu.Item>

            <SubMenu key="sub1" title={<span><Icon type="book"/><span></span></span>}>
              <Menu.Item key="sub1-1"><Link to="/main/assign/apply"></Link></Menu.Item>
              <Menu.Item key="sub1-2"><Link to="/main/assign/applyRecord"></Link></Menu.Item>
              <Menu.Item key="sub1-3"><Link to="/main/assign/preApprove"></Link></Menu.Item>
              <Menu.Item key="sub1-4"><Link to="/main/assign/hadApprove"></Link></Menu.Item>
            </SubMenu>

            <SubMenu key="sub2" title={<span><Icon type="bug" /><span></span></span>}>
              <Menu.Item key="sub2-1"><Link to="/main/accident/manage"></Link></Menu.Item>
              <Menu.Item key="sub2-2"><Link to="/main/accident/wounde"></Link></Menu.Item>
              <Menu.Item key="sub2-3"><Link to="/main/accident/statistic"></Link></Menu.Item>
            </SubMenu>

            <SubMenu key="sub3" title={<span><Icon type="fire"/><span></span></span>}>
              <Menu.Item key="sub3-1"><Link to="/main/risk/manage"></Link></Menu.Item>
              <Menu.Item key="sub3-2"><Link to="/main/risk/danger"></Link></Menu.Item>
              <Menu.Item key="sub3-3"><Link to="/main/risk/statistic"></Link></Menu.Item>
            </SubMenu>

            <SubMenu key="sub4" title={<span><Icon type="insurance" /><span></span></span>}>
              <SubMenu key="sub4-1" title="">
                <Menu.Item key="sub4-1-1"><Link to="/main/checktable/manage"></Link></Menu.Item>
                <Menu.Item key="sub4-1-2"><Link to="/main/firstIndicator/manage"></Link></Menu.Item>
                <Menu.Item key="sub4-1-3"><Link to="/main/secondIndicator/manage"></Link></Menu.Item>
              </SubMenu>
              <Menu.Item key="sub4-2"><Link to="/main/checktable/Input"></Link></Menu.Item>
              <Menu.Item key="sub4-3"><Link to="/main/checktable/result"></Link></Menu.Item>
              <Menu.Item key="sub4-4"><Link to="/main/hiddentTrouble/manage"></Link></Menu.Item>
              <Menu.Item key="sub4-5"><Link to="/main/hiddentTrouble/statistic"></Link></Menu.Item>
            </SubMenu>

            <SubMenu key="sub5" title={<span><Icon type="setting" /><span></span></span>}>
              <Menu.Item key="sub5-1"><Link to="/main/manage/organization"></Link></Menu.Item>
              <Menu.Item key="sub5-2"><Link to="/main/manage/role"></Link></Menu.Item>
              <Menu.Item key="sub5-3"><Link to="/main/manage/user"></Link></Menu.Item>
            </SubMenu>

          </Menu>
        </div>
        )
    }
}

export default LeftNav;