/**
 * 
 */
import React, { Component } from 'react';
import { Breadcrumb,  Divider, Button, message} from 'antd';
import { Form, Input, Radio, DatePicker, Switch } from 'antd';

import UpLoad from './upload';

class AddCheckTableResult extends Component {
    constructor(props) {
        super(props);

        this.state = {
            ishide: true
        };

        this.handleSwitch = this.handleSwitch.bind(this);
        this.getHiddenArea = this.getHiddenArea.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    /**
     * switch
     */
    handleSwitch() {
        this.setState({
            ishide: !this.state.ishide
        })
    }
    /**
     * 
     * @param {*} getFieldDecorator 
     */
    getHiddenArea(getFieldDecorator) {
        const ishide = this.state.ishide;
        if(ishide) {
            return null;
        }else {
            return (
                <div>
                    <Form.Item label="">
                        {getFieldDecorator('HDtype', {
                            rules: [{ required: true, message: '!' }],
                        })(
                            <Input maxLength="10" placeholder="" />,
                        )}
                    </Form.Item>
                    <Form.Item label="">
                        {getFieldDecorator('HDphoto', {
                            rules: [{ required: true, message: '!' }],
                        })(
                            <UpLoad action="/api/input/savePicture" />,
                        )}
                    </Form.Item>
                    
                </div>
                
            )
        }
    }


    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                console.log('Received values of form: ', values);
            }else {
                // console.log(err,values)
                message.info('!')
            }
        });
    }

    /**
     * render
     */
    render() {
        const mylist = [
            { 
                id: 1, 
                project: '',
                list: [
                    { secondLevelId: 11, content: '' },
                    { secondLevelId: 12, content: '' },
                    { secondLevelId: 13, content: '' },
                    { secondLevelId: 14, content: '' }
                ]
            },
            { 
                id: 5, 
                project: '' ,
                list: [
                    { secondLevelId: 11, content: '' },
                    { secondLevelId: 12, content: '' },
                    { secondLevelId: 13, content: '' },
                    { secondLevelId: 14, content: '' }
                ]
            }
        ];
 

        const { getFieldDecorator } = this.props.form;
        return (
            <div className="page">
                {/*  */}
                <Breadcrumb className="path">
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
                <div className="contentWrap" >
                    <Divider type="horizontal"></Divider>
                    <Form labelCol={{span:6}} wrapperCol={{span: 14}} onSubmit={this.handleSubmit} >
                        <Form.Item label="id">
                            {getFieldDecorator('name', {
                                rules: [{ required: true, message: '!' }],
                            })(
                                <Input placeholder="js" />,
                            )}
                        </Form.Item>

                        {/*  */}
                        {
                        }

                        
                        {/*   */}
                        <Form.Item label="">
                            {getFieldDecorator('type', {
                                rules: [{ required: true, message: '!' }],
                            })(
                                <Radio.Group>
                                    <Radio value="" ></Radio>
                                    <Radio value=""></Radio>
                                    <Radio value=""></Radio>
                                    <Radio value=""></Radio>
                                </Radio.Group>
                            )}
                        </Form.Item>
                        {/*  */}
                        <Form.Item label="">
                            {getFieldDecorator('isqualified', {
                                rules: [{ required: true, message: '!' }],
                            })(
                                <Radio.Group>
                                    <Radio value="1" checked></Radio>
                                    <Radio value="0"></Radio>
                                </Radio.Group>
                            )}
                        </Form.Item>
                        <Form.Item label="">
                            {getFieldDecorator('desc', {
                                rules: [{ required: false, message: '!' }],
                            })(
                                <Input.TextArea maxLength="20" placeholder="" />,
                            )}
                        </Form.Item>
                        {/*   */}
                        <Form.Item label="">
                            {getFieldDecorator('checkDate', {
                            rules: [{ required: true, message: '!' }],
                            })(
                                <DatePicker />,
                            )}
                        </Form.Item>

                        {/*  */}
                        <Form.Item label="">
                            {getFieldDecorator('username', {
                                rules: [{ required: true, message: '!' }],
                            })(
                                <Input maxLength="10" placeholder="" />,
                            )}
                        </Form.Item>
                        {/*  */}
                        <Form.Item label="">
                            {getFieldDecorator('otherPeople')(
                                <Input maxLength="20" placeholder="" />,
                            )}
                        </Form.Item>

                        {/*   */}
                        <Form.Item label="">
                            {getFieldDecorator('switch', { 
                                valuePropName: 'checked' 
                            })(
                                <Switch checkedChildren="" unCheckedChildren="" onChange={this.handleSwitch} />
                            )}
                        </Form.Item>

                        { this.getHiddenArea(getFieldDecorator) }
                        
                        <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
                            <Button type="primary" htmlType="submit">
                                Submit
                            </Button>
                        </Form.Item>
                    </Form>
                </div>
            </div>
        )
    }
}
export default Form.create({ name: 'checktable_result' })(AddCheckTableResult);


